// stories/o-step.stories.tsx
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OStep } from '../components/o-step/o-step';

export default {
	title: 'Components/Step',
	argTypes: {
		variant: {
			control: 'select',
			options: ['completed', 'active', 'default', 'error']
		}
	},
	args: {
		variant: 'default'
	}
} as Meta;

const TemplateDefault: StoryObj<OStep> = {
	args: {
		variant: 'default'
	},
	parameters: {
		layout: 'centered'
	},
	render: (args) => html` <o-step variant="${args.variant}" }>Default</o-step>`
};

const TemplateActive: StoryObj<OStep> = {
	args: {
		variant: 'active'
	},
	parameters: {
		layout: 'centered'
	},
	render: (args) => html` <o-step variant="${args.variant}" }>Active</o-step> `
};

const TemplateCompleted: StoryObj<OStep> = {
	args: {
		variant: 'completed'
	},
	parameters: {
		layout: 'centered'
	},
	render: (args) => html` <o-step variant="${args.variant}" }>Complete</o-step> `
};

const TemplateError: StoryObj<OStep> = {
	args: {
		variant: 'error'
	},
	parameters: {
		layout: 'centered'
	},
	render: (args) => html` <o-step variant="${args.variant}" }>Error</o-step> `
};

export const Default = {
	...TemplateDefault,
	args: { variant: 'default' },
	parameters: {
		layout: 'centered',
		docs: {
			source: {
				transform: (_, storyContext) => {
					const { args } = storyContext;
					//Create Dynamic code example using args
					return `
-----------------------
// Vue Code Snippet
-----------------------

import { OStepper } from "@oui/vue";
import { OStep } from "@oui/vue";

<o-stepper>
<o-step variant="${args.variant}">
  Step Label
</o-step>
</o-stepper>

-----------------------
// React Code Snippet
-----------------------

import { OStepper } from "@oui/react";
import { OStep } from "@oui/react";

<OStepper>
<OStep variant="${args.variant}">
  Step Label
</OStep>
</OStepper>
        `;
				}
			}
		}
	}
};

